.App {
  text-align: center;
  font-family: 'Righteous';
  background-color: #F7F2EA;
  min-height: 100vh;
}

.landing-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  min-height: 80vh;
  max-height: 90vh;
}

.title-container {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  padding: 0 16px;
}

.landing-page-title {
  font-size: 3rem;
  width: 100%;
}

.landing-page-subtitle {
  font-size: 1.5rem;
  width: 100%;
}

.landing-page-image-container {
  position: relative;
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  width: 100%;
  min-height: 40vh;
}

.landing-image {
  position: absolute;
  right: 0;
  bottom: 0;
  max-width: 100%;
  height: auto;
}

.visit-app-button {
  color: white;
  background-color: #CEAF7F;
  border: none;
  font-family: 'Righteous';
  padding: 20px;
  border-radius: 20px;;
}

.why-pennydao-container {
  margin: 36px 0 0 0;
}

.why-box-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.modal-form {
  display: flex;
  flex-direction: column;
}

.modal-form button {
  border: none;
  border-radius: 20px;
  margin: 0 10px;
  padding: 10px;
  font-family: 'Righteous';
  color: white;
}

.enabled {
  background-color: #CEAF7F;
}

.disabled {
  background-color: #C2C2C2;
}

.modal-form h2 {
  text-align: center;
}

.form-input-divider {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  grid-gap: 6%;
  gap: 6%;
}

.dashboardContainer {
