/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .landing-page-image-container {
    position: relative;
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    width: 100%;
    min-height: 60vh;
  }

  .landing-image {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 100%;
    max-height: auto;
  }

  .why-box-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }

  .dashboardContainer {
    padding-left: 15%;
  }

  .dashboardGrantStatusContainer {
    padding-left: 15%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .dashboardWalletContainer {
    display: flex;
    justify-content: flex-end;
  }

  .proposalContainer {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    margin-left: 50px;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .landing-container {
    display: grid;
    grid-template-columns: 40% 60%;
    min-height: 80vh;
    max-height: 90vh;
  }

  .title-container {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    align-self: center;
    padding: 0 0 0 25%;
    text-align: left;
  }

  .landing-page-title {
    font-size: 3rem;
  }

  .landing-page-image-container {
    position: relative;
    grid-column: 2 / 3;
    grid-row: 1 / 2;
  }

  .landing-image {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 90%;
    height: auto;
  }

  .form-input-divider {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .form-input {
    width: 40%;
  }

  select {
    width: 10%;
  }

  .searchButton {
    width: 10%;
  }
}

/* Portrait and Landscape */
@media only screen 
  and (min-width: 1024px) 
  and (max-height: 1366px) 
  and (-webkit-min-device-pixel-ratio: 1.5) {
    .landing-container {
      display: grid;
      grid-template-columns: 40% 60%;
      min-height: 30vh;
      max-height: 80vh;
    }

    .title-container {
      grid-column: 1 / 2;
      grid-row: 1 / 2;
      align-self: center;
      padding: 0 0 0 20%;
      text-align: left;
    }
  
    .landing-page-title {
      font-size: 3rem;
    }
  
    .landing-page-image-container {
      position: relative;
      grid-column: 2 / 3;
      grid-row: 1 / 2;
    }
  
    .landing-image {
      position: absolute;
      right: 0;
      bottom: 0;
      max-width: 90%;
      height: auto;
    }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {

  .landing-container {
    display: grid;
    grid-template-columns: 40% 60%;
    min-height: 80vh;
    max-height: 90vh;
  }

  .title-container {
